import React from 'react';
import Chart from 'react-google-charts';
import CircularProgress from '@material-ui/core/CircularProgress';
import { connect } from 'react-redux'
import SectionTitle from '../../components/section-title';

class OrdersProductCount extends React.Component {
  render() {
    const options = {
      backgroundColor: { fill: 'transparent' },
      colors: ['#00A0E3', '#577D35', '#FFDD00', '#A8DDE0'],
      animation: {
        duration: 1000,
        easing: 'out',
        startup: true,
      },
    };

    return (
      <React.Fragment>
        <SectionTitle>Opportunity product count</SectionTitle>
        {!this.props.loaded && <CircularProgress />}
        {this.props.loaded && <Chart
          width={'100%'}
          height={'300px'}
          chartType="BarChart"
          backgroundColor="transparent"
          data={this.props.data}
          options={options}
        />}
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    data: state.websocket.orderProductCount,
    loaded: state.websocket.orderProductCountLoaded
  }
};

export default connect(mapStateToProps)(OrdersProductCount)