import React from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';
import Chart from 'react-google-charts';
import { connect } from 'react-redux'
import SectionTitle from '../../components/section-title';

class CaseStatuses extends React.Component {
  render() {
    return (
      <React.Fragment>
        <SectionTitle>Todays cases</SectionTitle>
        {!this.props.loaded && <CircularProgress />}
        {this.props.loaded && <Chart
          width={this.props.chart.width}
          height={this.props.chart.height}
          chartType={this.props.chart.chartType}
          loader={<div>Loading Chart</div>}
          data={this.props.data}
          options={this.props.chart.options}
        />}
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    data: state.websocket.caseStatuses,
    loaded: state.websocket.caseStatusesLoaded,
    chart: state.websocket.caseStatusesChart
  }
};

export default connect(mapStateToProps)(CaseStatuses)