import React from 'react';
import clsx from 'clsx';

import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Page from '../../components/page';

import Recent from './recent';
import OrdersProductCount from './../charts/orders-product-count';
import OpportunitiesStatusRevenues from './../charts/opportunties-status-revenues';
import CaseStatuses from './../charts/case-statuses';
import LinearProgress from '@material-ui/core/LinearProgress';
import { connect } from 'react-redux'

class HomeScreen extends React.Component {

  render() {
    const classes = this.props.classes;
    const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);

    return (
      <Page title="Dashboard" icon="dashboard">

        <Grid container spacing={6}>
          {/* Client info */}
          <Grid item xs={12} md={12} lg={12}>
            {!this.props.loaded && <LinearProgress variant="query" />}
            {this.props.loaded &&
              <Grid container spacing={3}>
                <Grid item xs={12} md={6} lg={4}>
                  <div className="cloud">
                    <div className="cloudshadow">
                    </div>
                    <div className="cloud-content">
                      <Recent title="Opportunities won today" classes={classes} value={this.props.dashboardWonToday} />
                    </div>
                  </div>
                </Grid>

                <Grid item xs={12} md={6} lg={4}>
                  <div className="cloud">
                    <div className="cloudshadow">
                    </div>
                    <div className="cloud-content">
                      <Recent title="Cases closed today" classes={classes} value={this.props.dashboardCasesClosed} />
                    </div>
                  </div>
                </Grid>

                <Grid item xs={12} md={6} lg={4}>
                  <div className="cloud">
                    <div className="cloudshadow">
                    </div>
                    <div className="cloud-content">
                      <Recent title="Revenue generated today" classes={classes} value={this.props.dashboardRevenueToday} />
                    </div>
                  </div>
                </Grid>
              </Grid>
            }
          </Grid>
          {/* Chart */}
          <Grid item xs={12} md={6} lg={6}>
            <div className="cloud-container">
              <div className="cloud mini"></div>
              <Paper className={fixedHeightPaper}>
                <OpportunitiesStatusRevenues />
              </Paper>
            </div>
          </Grid>
          <Grid item xs={12} md={6} lg={6}>
            <div className="cloud-container">
              <div className="cloud mini"></div>
              <Paper className={fixedHeightPaper}>
                <OrdersProductCount />
              </Paper>
            </div>
          </Grid>
          {/* Recent Deposits */}
          <Grid item xs={12} md={6} lg={6}>
            <div className="cloud-container">
              <div className="cloud mini"><div className="cloudshadow mini"></div></div>
              <Paper className={fixedHeightPaper}>
                <CaseStatuses />
              </Paper>
            </div>
          </Grid>
        </Grid>

      </Page >
    );
  }
}

const mapStateToProps = state => {
  return {
    loaded: state.websocket.dashboardLoaded,
    dashboardWonToday: state.websocket.dashboardWonToday,
    dashboardCasesClosed: state.websocket.dashboardCasesClosed,
    dashboardRevenueToday: state.websocket.dashboardRevenueToday,
  }
};

export default connect(mapStateToProps)(HomeScreen)
