/* eslint-disable no-script-url */
import React from 'react';
import { connect } from 'react-redux'

import SectionTitle from '../../components/section-title';
import Grid from '@material-ui/core/Grid';
import EditIcon from '@material-ui/icons/Edit';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';

function ListItemLink(props) {
  return <div style={{ 'textAlign': 'right' }}>
    <ListItem button component="a" {...props} />
  </div>;
}

class Profile extends React.Component {

  render() {
    return (
      <React.Fragment>
        <Grid container>
          <Grid item lg={11} md={11} xs={9}>
          </Grid>
          <Grid item lg={1} md={1} xs={3}>
            <ListItem>
              <ListItemIcon>
                <ListItemLink href={this.props.data.userediturl} target="_blank">
                  <EditIcon className={this.props.classes.pageIcon} />
                </ListItemLink>
              </ListItemIcon>
            </ListItem>
          </Grid>
        </Grid>
        <Grid container>
          <Grid item lg={12} md={12} xs={12} className={this.props.classes.centeralign}>
            <img src={this.props.data.fullPhotoUrl} alt="Avatar" className={this.props.classes.avatarradius} />
          </Grid>
          <Grid container className={this.props.classes.section}>
            <Grid container>
              <Grid item lg={6} md={6} xs={12} className={this.props.classes.section}>
                <SectionTitle component="h3" variant="h6">User Details</SectionTitle>

                <Grid container spacing={1} mb={100}>
                  <Grid item md={4} xs={5}>Username</Grid>
                  <Grid item md={8} xs={6}>{this.props.data.username}</Grid>

                  <Grid item md={4} xs={5}>Email</Grid>
                  <Grid item md={8} xs={6}>{this.props.data.email}</Grid>

                  <Grid item md={4} xs={5}>Alias</Grid>
                  <Grid item md={8} xs={6}>{this.props.data.alias}</Grid>

                  <Grid item md={4} xs={5}>Nickname</Grid>
                  <Grid item md={8} xs={6}>{this.props.data.nickname}</Grid>

                  <Grid item md={4} xs={5}>Phone</Grid>
                  <Grid item md={8} xs={6}>{this.props.data.phone}</Grid>

                  <Grid item md={4} xs={5}>Extension</Grid>
                  <Grid item md={8} xs={6}>{this.props.data.extension}</Grid>

                  <Grid item md={4} xs={5}>Fax</Grid>
                  <Grid item md={8} xs={6}>{this.props.data.fax}</Grid>

                  <Grid item md={4} xs={5}>Mobile</Grid>
                  <Grid item md={8} xs={6}>{this.props.data.mobilephone}</Grid>
                </Grid>
              </Grid>

              <Grid item lg={6} md={6} xs={12} className={this.props.classes.section}>
                <SectionTitle component="h3" variant="h6">Address</SectionTitle>

                <Grid container spacing={1}>
                  <Grid item md={4} xs={5}>Street</Grid>
                  <Grid item md={8} xs={6}>{this.props.data.street}</Grid>

                  <Grid item md={4} xs={5}>City</Grid>
                  <Grid item md={8} xs={6}>{this.props.data.city}</Grid>

                  <Grid item md={4} xs={5}>State / Province</Grid>
                  <Grid item md={8} xs={6}>{this.props.data.state}</Grid>

                  <Grid item md={4} xs={5}>Zip / Postal Code</Grid>
                  <Grid item md={8} xs={6}>{this.props.data.postalcode}</Grid>

                  <Grid item md={4} xs={5}>Country</Grid>
                  <Grid item md={8} xs={6}>{this.props.data.country}</Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </React.Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    data: state.page.profile,
    loaded: state.websocket.ordersLoaded
  }
};

export default connect(mapStateToProps)(Profile)