import {
  ACTION_OPPORTUNITY_STATUS_REVENUE,
  ACTION_ORDER_PRODUCT_COUNT,
  ACTION_CASE_STATUSES,
  ACTION_ORDERS,
  ACTION_DASHBOARD_WON_TODAY,
  ACTION_DASHBOARD_CASES_CLOSED,
  ACTION_DASHBOARD_REVENUE_TODAY,
} from '../actions/websocket';

const initialState = {
  dashboardLoaded: false,

  dashboardWonToday: 0,
  dashboardWonTodayHash: '',

  dashboardCasesClosed: 0,
  dashboardCasesClosedHash: '',

  dashboardRevenueToday: 0,
  dashboardRevenueTodayHash: '',

  opportunityStatusRevenue: [],
  opportunityStatusRevenueHash: '',
  opportunityStatusRevenueLoaded: false,
  opportunityStatusRevenueChart: [],

  orderProductCount: [],
  orderProductCountHash: '',
  orderProductCountLoaded: false,

  caseStatuses: [],
  caseStatusesHash: '',
  caseStatusesLoaded: false,
  caseStatusesChart: [],

  orders: [],
  ordersHash: '',
  ordersLoaded: false,
};

export default function(state = initialState, action) {
  let dataStructure;

  switch (action.type) {
    case ACTION_OPPORTUNITY_STATUS_REVENUE:
      // If the hash is the same, the data is unchanged
      if (action.payload.hash === state.opportunityStatusRevenueHash) {
        return state;
      }

      // Hash values do not match, data has changed
      dataStructure = [];
      dataStructure.push(action.payload.data.data.entities);
      action.payload.data.data.values.forEach((item, index) => {
        dataStructure.push(item);
      });

      return Object.assign({}, state, {
        opportunityStatusRevenue: dataStructure,
        opportunityStatusRevenueLoaded: true,
        opportunityStatusRevenueHash: action.payload.hash,
        opportunityStatusRevenueChart: action.payload.data.meta,
      });
    case ACTION_ORDER_PRODUCT_COUNT:
      // If the hash is the same, the data is unchanged
      if (action.payload.hash === state.orderProductCountHash) {
        return state;
      }

      // Hash values do not match, data has changed
      return Object.assign({}, state, {
        orderProductCount: action.payload.data,
        orderProductCountHash: action.payload.hash,
        orderProductCountLoaded: true,
      });
    case ACTION_CASE_STATUSES:
      // If the hash is the same, the data is unchanged
      if (action.payload.hash === state.caseStatusesHash) {
        return state;
      }

      // Hash values do not match, data has changed
      dataStructure = [];
      dataStructure.push(action.payload.data.data.entities);
      action.payload.data.data.values.forEach((item, index) => {
        dataStructure.push(item);
      });

      return Object.assign({}, state, {
        caseStatuses: dataStructure,
        caseStatusesLoaded: true,
        caseStatusesHash: action.payload.hash,
        caseStatusesChart: action.payload.data.meta,
      });
    case ACTION_ORDERS:
      // If the hash is the same, the data is unchanged
      if (action.payload.hash === state.ordersHash) {
        return state;
      }

      // Hash values do not match, data has changed
      return Object.assign({}, state, {
        orders: action.payload.data,
        ordersLoaded: true,
        ordersHash: action.payload.hash,
      });
    case ACTION_DASHBOARD_WON_TODAY:
      // If the hash is the same, the data is unchanged
      if (action.payload.hash === state.dashboardWonTodayHash) {
        return state;
      }

      // Hash values do not match, data has changed
      return Object.assign({}, state, {
        dashboardWonToday: action.payload.data,
        dashboardLoaded: true,
        dashboardWonTodayHash: action.payload.hash,
      });
    case ACTION_DASHBOARD_CASES_CLOSED:
      // If the hash is the same, the data is unchanged
      if (action.payload.hash === state.dashboardCasesClosedHash) {
        return state;
      }

      // Hash values do not match, data has changed
      return Object.assign({}, state, {
        dashboardCasesClosed: action.payload.data,
        dashboardLoaded: true,
        dashboardCasesClosedHash: action.payload.hash,
      });
    case ACTION_DASHBOARD_REVENUE_TODAY:
      // If the hash is the same, the data is unchanged
      if (action.payload.hash === state.dashboardRevenueTodayHash) {
        return state;
      }

      // Hash values do not match, data has changed
      return Object.assign({}, state, {
        dashboardRevenueToday: action.payload.data,
        dashboardLoaded: true,
        dashboardRevenueTodayHash: action.payload.hash,
      });
    default:
      return state;
  }
}
